:root {
  font-family: "Roboto", sans-serif;
}

.slider__btn-wrapper {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-around;
}
.slider__btn-wrapper .slider__btn-next i,
.slider__btn-wrapper .slider__btn-prev i {
  font-size: 32px;
  color: #333333;
  transition: color 0.4s linear;
  cursor: pointer;
}
.slider__btn-wrapper .slider__btn-next i:hover,
.slider__btn-wrapper .slider__btn-prev i:hover {
  color: #f57f31;
}
.slider__btn-wrapper .slider__btn-next::after,
.slider__btn-wrapper .slider__btn-prev::after {
  font-size: 2rem;
  color: white;
}
.slider__btn-wrapper .slider__button-prev {
  right: 30rem;
}

.testimonials {
  margin-top: 10rem;
}
.testimonials__wrapper {
  width: 100%;
  padding-top: 20px;
  text-align: center;
  color: #000;
  display: flex;
  justify-content: center;
  position: relative;
}
.testimonials__bg {
  top: -48px;
  position: absolute;
  width: 85%;
  height: 316px;
  border-radius: 24px;
  background-color: #f57f31;
}
@media (max-width: 599px) {
  .testimonials__bg {
    top: -8px;
    width: 95%;
    height: 200px;
  }
}
.testimonials__btn {
  padding: 0 16rem;
  margin-top: 32px;
  display: flex;
}
@media (max-width: 599px) {
  .testimonials__btn {
    display: none;
  }
}
.testimonials .slider {
  text-align: left;
  width: 100%;
}
.testimonials .slider .slick-slide {
  margin: 32px;
  padding: 8px 64px 16px 32px;
  box-shadow: 0px 8px 8px rgba(0, 0, 0, 0.15);
  border-radius: 24px;
  background-color: white;
  border: 2px solid transparent;
  transition: all 0.4s linear;
  cursor: pointer;
}
.testimonials .slider .slick-slide.slick-current.slick-active.slick-center {
  border: 2px solid #f5b431;
}
.testimonials .slider .slick-slide.slick-current.slick-active.slick-center img {
  border: 2px solid #f5b431;
}
@media (max-width: 599px) {
  .testimonials .slider .slick-slide {
    font-size: 0.8rem;
    margin: 8px;
    padding: 16px;
    padding-top: 0px;
    text-align: center;
  }
}
@media (min-width: 600px) {
  .testimonials .slider .slick-slide {
    padding: 16px;
  }
}
.testimonials .slider__img {
  width: 72px;
  height: 72px;
  margin-top: -48px;
  margin-bottom: 16px;
  border-radius: 50%;
  object-fit: cover;
}
@media (max-width: 599px) {
  .testimonials .slider__img {
    width: 56px;
    height: 56px;
    margin-top: -8px;
  }
}
.testimonials .slider__heading {
  color: #f57f31;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 0.5em;
  margin-bottom: 0;
  line-height: 4px;
}
@media (max-width: 599px) {
  .testimonials .slider__heading {
    font-size: 0.7em;
    display: inline;
    line-height: 4px;
  }
}
.testimonials .slider__title {
  font-size: 2em;
}
.testimonials .slider__subtitle {
  font-size: 1em;
  margin-bottom: 1em;
  margin-top: 0;
}
@media (max-width: 599px) {
  .testimonials .slider__subtitle {
    margin-top: 8px;
    font-size: 0.8em;
  }
}
.testimonials .slider__description {
  font-size: 1em;
  line-height: 24px;
}
@media (max-width: 599px) {
  .testimonials .slider__description {
    line-height: 16px;
  }
}